<template>
  <b-nav class="text-center">
    
    <b-container>
      <b-row>
        <b-col>
          Tasks:
          <b-badge
            v-for="task in tasks"
            :variant="task.style"
          >{{ task.keyword }} {{ task.progress }}</b-badge>
        </b-col>
        <b-col>
          Engines:
          <b-badge
            v-for="engine in engines"
            :variant="[engine.alive ? 'success' : 'secondary']"
          >{{ engine.name }}</b-badge>
        </b-col>
      </b-row>
    </b-container>
  </b-nav>
</template>

<script>
export default {
  name: "StatusBar",
  data() {
    return {
        abcde:8461616,
      tasks: [
        {
          keyword: "机械师",
          progress: 55,
          style: "secondary"
        },
        {
          keyword: "蝙蝠侠",
          progress: 23,
          style: "primary"
        }
      ],
      engines: [
        {
          name: "苗～",
          alive: true
        },
        {
          name: "汪～",
          alive: true
        },
        {
          name: "RAS",
          alive: false
        }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



