<template>
  <ButtonAndModal :item="sup">
    <template v-slot:modalContent>
      <b-spinner v-if="!sup.loaded" variant="success" type="grow" label="Spinning"></b-spinner>
      <b-table v-else striped hover :items="sup.data"></b-table>
    </template>
  </ButtonAndModal>
</template>

<script>
import ButtonAndModal from "./ButtonAndModal";
export default {
  name: "SupBM",
  components: {
    ButtonAndModal
  },
  methods: {
    stirModal() {
      console.log("Sup clicked!");
      this.ModalOn = !this.ModalOn;
    }
  },
  data() {
    return {
        sup: {
          info: "支持",
          act: this.stirModal,
          style: "info",
          modalOn: true,
          loaded: true,
          data: [
            {
              name: "张三",
              msg: "bing.com",
              assert: "100RMB"
            },
            {
              name: "李四",
              msg: "google.com",
              assert: "100RMB"
            },
            {
              name: "王五",
              msg: "baidu.com",
              assert: "100RMB"
            }
          ]
        },
  
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



