<template>
  <div id="app">
   
    <StatusBar></StatusBar>
    <DigBar :resBoardOn='resBoardOn' ></DigBar>
    <SuspArea>
      <template v-slot>
        <MsgBM></MsgBM>
        <SupBM></SupBM>
        <HelpBM></HelpBM>
        <MoreBM></MoreBM>
      </template>
    </SuspArea>
    <ResBoard v-if="resBoardOn" :resBoardOn='resBoardOn' ></ResBoard>
    <!-- <Test></Test> -->
  </div>
</template>

<script>
import Test from "./components/Test";
import SuspArea from "./components/SuspArea";
import StatusBar from "./components/StatusBar"
import DigBar from "./components/DigBar"
import MsgBM from "./components/MsgBM"
import SupBM from "./components/SupBM"
import HelpBM from "./components/HelpBM"
import MoreBM from "./components/MoreBM"

import ResBoard from "./components/ResBoard"

export default {
  name: "App",
  components: {
    Test,
    SuspArea,
    StatusBar,
    DigBar,
    MsgBM,
    SupBM,
    HelpBM,
    MoreBM,
    ResBoard
  },
  methods: {},
  data() {
    return {
      homeLink: "https://resdig.cn",
      resBoardOn: false,
    };
  }
};
</script>
<style>
.speaceX {
  margin-top: 5%;
  margin-bottom: 5%;
}

.speaceY {
  margin-top: 5%;
  margin-bottom: 5%;
}

.speace {
  margin: 5%;
}
.textCenter {
  text-align: center;
}

</style>



