<template>
  <ButtonAndModal :item="msg">
    <template v-slot:modalContent>
      <b-spinner v-if="!msg.loaded" variant="success" type="grow" label="Spinning"></b-spinner>
      <b-table v-else striped hover :items="msg.data"></b-table>
    </template>
  </ButtonAndModal>
</template>

<script>
import ButtonAndModal from "./ButtonAndModal";
export default {
  name: "MsgBM",
  components: {
    ButtonAndModal
  },
  methods: {
    stirModal() {
      console.log("Msg clicked!");
      this.ModalOn = !this.ModalOn;
    },
    sendMsg() {
      console.log("send message!");
      console.log(this.message);
    }
  },
  data() {
    return {
      msg: {
        info: "留言",
        act: this.stirModal,
        style: "info",
        modalOn: false,
        loaded: true,
        data: [
          {
            time: 12345.5,
            content: "niupi"
          },
          {
            time: 12345.5,
            content: "niupi"
          },
          {
            time: 12345.5,
            content: "niupi"
          }
        ]
      }
    };
  }
};
</script>

