<template>
  <b-container :class="resBoardOn ? 'textCenter' : 'textCenter pageCenter'">
    <b-row>
      <b-col></b-col>
      <b-col sm="10" md="8" lg="6">
        <h1>Resdig</h1>
        <b-input-group>
          <b-form-input v-model="keyword" placeholder="请输入搜索关键词"></b-form-input>
          <b-input-group-append>
            <b-button @click="dig" variant="success">Dig</b-button>
          </b-input-group-append>
        </b-input-group>
        <GuessList :keyword='keyword'></GuessList>
      </b-col>
      <b-col></b-col>
    </b-row>
  </b-container>
</template>

<script>
import GuessList from "./GuessList";


export default {
  name: "DigBar",
  components:{
    GuessList
  },
  props: ["resBoardOn"],
  methods: {
    dig() {

      console.log("Dig clicked!");
      console.log(this.keyword, this.resBoardOn);
    },
   
  },
  data() {
    return {
      keyword: "",
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pageCenter {
  position: absolute;
  margin: auto;
  width: 100%;
  top: 25%;
  left: 0;
  right: 0;
}
</style>



